import React, { Component } from 'react';
import Item from "./Items"
import AhsanManjil from "../Images/Dhaka/AhsanManjil.jpg"
import Hatirjheel from "../Images/Dhaka/Hatirjheel.jpg"
class Dhaka extends Component {
    render() {
        let AhsanDetails = "আহসান মঞ্জিল পুরান ঢাকার ইসলামপুরের কুমারটুলী এলাকায় বুড়িগঙ্গা নদীর তীরে অবস্থিত। এটি পূর্বে ছিল ঢাকার নবাবদের আবাসিক প্রাসাদ ও জমিদারীর সদর কাচারি। বর্তমানে এটি জাদুঘর হিসাবে ব্যবহৃত হচ্ছে। এর প্রতিষ্ঠাতা নওয়াব আবদুল গনি।তিনি তার পুত্র খাজা আহসানুল্লাহ-র নামানুসারে এর নামকরণ করেন। ১৮৫৯ খ্রিষ্টাব্দে আহসান মঞ্জিলের নির্মাণ কাজ শুরু হয়ে ১৮৭২ খ্রিষ্টাব্দে সমাপ্ত হয়। ১৯০৬ খ্রিষ্টাব্দে এখানে এক অনুষ্ঠিত বৈঠকে মুসলিম লীগ প্রতিষ্ঠার সিদ্ধান্ত হয়। আহসান মঞ্জিল কয়েকবার সংস্কার করা হয়েছে। সর্বশেষ সংস্কার করা হয়েছে অতি সম্প্রতি। এখন এটি বাংলাদেশ জাতীয় জাদুঘর কর্তৃক পরিচালিত একটি জাদুঘর।"
        let HatirJheelDetails = "হাতিরঝিল বাংলাদেশের রাজধানী ঢাকার একটি এলাকা যা জনসাধারণের চলাচলের জন্য তৈরী করা হয়েছে। এ প্রকল্প এলাকাটি উদ্ধোধন ও জনসাধারণের জন্য উন্মুক্ত হয় ২০১৩ সালের ২ জানুয়ারি। এ প্রকল্প চালুর ফলে ঢাকার তেজগাঁও, গুলশান, বাড্ডা, রামপুরা, মৌচাক, ফার্মগেট, কারওয়ান বাজার ও মগবাজার এলাকার বাসিন্দাসহ এ পথ দিয়ে চলাচলকারী যাত্রীরা বিশেষ সুবিধা পাচ্ছেন। হাতিরঝিল প্রকল্পটি বাস্তবায়ন ও তদারকি করছে বাংলাদেশ সেনাবাহিনীর 'স্পেশাল ওয়ার্কস অরগানাইজেশন' (এসডব্লিউও)। এ প্রকল্পের অন্যতম মূল লক্ষ্য হচ্ছে বৃষ্টির পানি সংরক্ষণ, জলাবদ্ধতা ও বন্যা প্রতিরোধ, ময়লা পানি নিষ্কাশন ব্যবস্থার উন্নয়ন, রাজধানীর যানজট নিরসন এবং শ্রীবৃদ্ধি করা। "
        return (
            <div className = "container">
                <div className="row">
                    <div className="col-sm-6 justify-content-center d-flex">
                        <Item name = "আহসান মঞ্জিল" details = {AhsanDetails} pic = {AhsanManjil}></Item>
                    </div>
                    <div className="col-sm-6 justify-content-center d-flex">
                    <Item name = "হাতিরঝিল" details = {HatirJheelDetails} pic = {Hatirjheel}></Item>
                    </div>
                </div>
            </div>
        );
    }
}

export default Dhaka;